<!--难点：
在组件中递归使用组件
在当前组件抛出事件时候，子组件要接收事件（想象成内部就在使用此组件），并继续往上抛，因为内部已经传递了一个参数给它，所以直接调用即可
 -->
<template>
  <ul class="right-list-container">
    <li v-for="(item, i) in list" :key="i">
      <span @click="handleClick(item)" :class="{ active: item.isSelect }">
        {{ item.name }}
      </span>
      <span
        v-if="item.aside"
        @click="handleClick(item)"
        class="aside"
        :class="{ active: item.isSelect }"
      >
        {{ item.aside }}
      </span>
      <!-- 显示当前组件 -->
      <RightList :list="item.children" @select="handleClick" />
    </li>
  </ul>
</template>

<script>
export default {
  name: 'RightList',
  props: {
    // [ {name:"xxx", isSelect:true, children:[ {name:"yyy", isSelect: false} ] } ]
    list: {
      type: Array,
      default: () => [], //当默认值是对象或者数组的时候要用函数的返回结果
    },
  },
  methods: {
    handleClick(item) {
      if (!item.isSelect) {
        this.$emit('select', item);
      }
    },
  },
};
</script>

<style scoped lang="less">
@import '~@/style/var.less';
.right-list-container {
  list-style: none;
  padding: 0;
  .right-list-container {
    margin-left: 1em;
  }
  li {
    min-height: 40px;
    line-height: 40px;
    font-size: 14px;
    cursor: pointer;
    .active {
      color: @warn;
      font-weight: bold;
    }
  }
}
.aside {
  font-size: 12px;
  margin-left: 1em;
  color: @gray;
}
</style>
